<template>
		<div class="my-hold-con">
				<custHeader :title="title"></custHeader>
        <div style="">
          <div class="my-hold-info-con" style="height:126px;">
            <mt-swipe :auto="0" class="personal-info">
            
                  <mt-swipe-item>
                    <div class="normal-box">
                            <div>
                                <div ><div class="personal-info-t">总资产</div><div>{{totalAssets | toMoney }}</div></div>
                                <div><div class="personal-info-t">总市值</div><div>{{mktVal | toMoney}}</div></div>
                            </div>
                            <div>
                                <div><div class="personal-info-t">可取</div><div>{{fundWithDraw | toMoney}}</div></div><div>
                                <div><div class="personal-info-t">可用</div><div>{{fundAvl | toMoney}}</div></div>
                            </div>
                            <div v-if=false>
                                <div><div>当日盈亏</div><div :class="styleColor(floatingSameDat,0)">{{floating | toMoney}}</div></div>
                <div>浮动盈亏</div><div :class="styleColor(floating,0)">{{floatingSameDat | toMoney}}</div></div>
                            </div>
                    </div>
                </mt-swipe-item>
            </mt-swipe>
            </div>
            <div class="" style="position:relative;margin-top:12px;">
              <div class="hold-con">
                <ul class="my-hold-container">
                    <li>
                             <div>市值</div>
                             <div class="text-right">盈亏</div>
                             <div class="text-right">持仓/可用</div>
                             <div class="text-right">成本/现价</div>
                    <div class="text-right">实时盈亏</div>
                             </li>
                             <li v-for="(item,index) in myHold" @click="extendHold(index)" :class="{'paddingBottom':selectedHold==index}">
                                <div><div>{{item.stkName}}</div><div>{{item.mktVal}}</div></div>
                                <div class="text-right" :class="styleColor(item.stkPlamt,0)"><div>{{item.stkPlamt}}</div><div>{{jsYK(item)}}</div></div>
                                <div class="text-right"><div>{{item.stkBln}}</div><div>{{item.stkAvl}}</div></div>
                                <div class="text-right"><div>{{item.stkBcost}}</div><div>{{item.stkBcostRlt}}</div></div>
                          <div class="text-right" :class="styleColor(item.stkPlamtRlt,0)"><div>{{item.stkPlamtRlt}}</div><div>{{jsSSYK(item)}}</div></div>
                             </li>
                             <li v-show="myHold.length==0" style="border:none;margin-top:40px;text-align:center;">  当前没有持仓</li>
                         </ul>

                    </div>
              <ul class="my-hold-container2" v-show="myHold.length!==0">
                   <li>
                   <div>市值</div>
                   </li>
                   <li v-for="(item,index) in myHold" @click="extendHold(index)" :class="{'paddingBottom':selectedHold==index}">
                    <div><div>{{item.stkName}}</div><div>{{item.mktVal}}</div></div>
                   </li>
                 </ul>
                 <div class="normal-box my-hold-container3" v-show="selectedHold!=-1" :style="{'top':expendTop}">
                   <div @click="tradeBuy" class="border-right">买入</div>
                   <div @click="tradeSell"> 卖出</div>
                 </div>
            </div>
            </div>
                
      			

		</div>
</template>
<script>
import cookieUtil from 'js-cookie'
import custMixin from  'util/custMixin'
import {MessageBox} from 'mint-ui'
import {Swipe,SwipeItem} from 'mint-ui'
import custHeader from  'components/custHeaderNoBack'
Vue.component(Swipe.name,Swipe);
Vue.component(SwipeItem.name,SwipeItem);
export default{
    data(){
        return {
		selectedHold:-1,
		title:"我的持仓",
         myHold:[ ],
                totalAssets:0.00,//总资产
                mktVal:0.00,//市值
                floating:0.00,
                floatingSameDat:0.00,
                fundAvl:0.00,//资金可用
                fundWithDraw:0.00//资金可取
            }
    },
    created:function(){
    		this.getMyHold();
            this.qryCustSumAsset();
    },
    mixins:[custMixin],
    components: {
		 custHeader
    },
    computed:{
        // styleColor:function(){
        //     return this.floating<0? "sub-color":"add-color";
        // }
		expendTop:function(){
		return (29+32+this.selectedHold*45)+"px";
	}

    },
    methods:{
			tradeBuy:function(){
				var pars={
		            hqzqdm:this.myHold[this.selectedHold].stkCode,
		        }
				this.$router.push({path:"/trade/buyIn",query:pars});
			},
			tradeSell:function(){
				var pars={
		            hqzqdm:this.myHold[this.selectedHold].stkCode,
		        }
				this.$router.push({path:"/trade/sellOut",query:pars});
			},
			extendHold:function(index){
				if(this.selectedHold!=-1){
					this.selectedHold=-1;
				}else{
					this.selectedHold=index;
				}
			},
         getMyHold:function(){
          var pars={
             sort:'STK_AVL',
             order:'desc',
             limit:1000,
             offset:0,
             stkex:global.scdm
             }
                axiosUtil.myHold(pars).then(data=>{
                    var data=data.data;
                    this.myHold=data.rows;
                    var allMktVal=0,floating=0,floatingSameDat=0,stkAvl=0,stkBln=0;
                    data.rows.forEach(function(item,index){
                        allMktVal+=item.allMktVal;
                        floating+=item.stkPlamt;
                        floatingSameDat+=item.stkPlamt;
                        stkAvl+=item.stkAvl;
                        stkBln+=item.stkBln;
                    });
                    // this.allMktVal=allMktVal;
                    // this.floating=floating;
                    // this.floatingSameDat=floatingSameDat;
                    // this.stkAvl=stkAvl;
                    // this.stkBln=stkBln;
                })
                },
   //           getMyAsset:function(){
   //           var pars={
   //           sort:"PUB_TIME",
   //           order:"desc",
   //           limit:20,
   //           offset:0
   //           };
   //              axiosUtil.fundAssetQry(pars).then(data=>{
   //                  var data=data.data;
   //                  var allAsset=0;
   //                   data.rows.forEach(function(item,index){
   //                      allAsset+=item.fundBln;
   //                   })
   //                   this.allAsset=allAsset;
   //              })
			// },
                qryCustSumAsset:function(){
                   axiosUtil.qryCustSumAsset().then(data=>{
                    var data=data.data;

                     this.fundWithDraw=data.data.fundWithDraw;
                     this.mktVal=data.data.mktVal;
                     this.totalAssets=data.data.totalAssets;
                     this.fundAvl=data.data.fundAvl;
                 })
                },

			jsYK:function(item){
				if(item.stkBcost){
						return item.stkPlamt.div(item.stkBcost)>10?item.stkPlamt.div(item.stkBcost).toFixed(2)+"倍":item.stkPlamt.div(item.stkBcost).mul(100).toFixed(2)+"%";
				}
				return 0;
			},
			jsSSYK:function(item){
				if(item.stkBcost){
				return item.stkPlamtRlt.div(item.stkBcost)>10?item.stkPlamtRlt.div(item.stkBcost ).toFixed(2)+"倍":item.stkPlamtRlt.div(item.stkBcost).mul(100).toFixed(2)+"%";
				}
				return 0;
			}
    }
}
</script>
<style scoped>
.normal-box{
   padding: 0 12px;
    text-align: left;

        margin: 12px 0;
}
.paddingBottom{
	padding-bottom: 51px;
}
.normal-box>div>div>div:first-child{
    color: #999;
}
.normal-box>div>div>div:nth-child(2){
font-weight: 600;
}
.normal-box>div>div{
    margin-bottom: 18px;
}
.personal-info{
 /*   height: 120px;*/
}

.my-hold-con{
    flex-direction: column;
    display: flex;
    height: 100%;
    padding-bottom: 26px;
   }
   .hold-con{
   flex-grow:1;
   background:#fff;

   }
   .my-hold-container3{
   	height: 45px;
	width: 100%;
	position: absolute;
	top: 0;
	z-index: 4;
    text-align: center;
    border: 1px solid #eee;
    line-height: 45px;
   }
   .border-right{
   	border-right: 1px solid #eee;
   }
   .my-hold-container{
	   width: 125vw;
      content: 'viewport-units-buggyfill; width: 125vw;';
   }
   .personal-info-t{
   	font-size: 12px;
	margin-bottom: 6px;
        
   }
   .my-hold-info-con{
    height: 124px;

   }
 .my-hold-info-con .mint-swipe{
  height: 100%;
 }
</style>



